<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-lx-cascades"></i> 视频列表
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          icon="el-icon-delete"
          class="handle-del mr10"
          @click="delAllSelection"
        >批量删除</el-button>
        <el-select
          v-model="type_1"
          placeholder="一级分类"
          class="handle-select mr10"
          @change="changeType"
        >
          <el-option label="无" value=""></el-option>
          <el-option
            v-for="(item,index) in typeList"
            :key="index"
            :label="item.name"
            :value="index"
          ></el-option>
        </el-select>
        <el-select v-model="query.type" placeholder="二级分类" class="handle-select mr10">
          <el-option label="无" value=""></el-option>
          <el-option
            v-for="(item,index) in subTypeList"
            :key="index"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-input v-model="query.name" placeholder="标题" class="handle-input input-title  mr10"></el-input>
        <el-input v-model="query.uid" placeholder="用户ID" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
      </div>
      <el-table
        :data="tableData"
        border
        class="table"
        ref="multipleTable"
        header-cell-class-name="table-header"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="id" label="ID" width="55" align="center"></el-table-column>
        <el-table-column prop="title" label="标题"></el-table-column>
        <el-table-column prop="type_name" label="类型" align="center"></el-table-column>
        <el-table-column label="发布用户" align="center">
          <template slot-scope="scope">
            {{scope.row.name==null?'未设置':scope.row.name}}
          </template>
        </el-table-column>
        <el-table-column label="视频链接" align="center">
          <template slot-scope="scope">
            <el-link :href="$utils.formatUrl(scope.row.url)" type="primary" target="_blank">点击查看</el-link>
          </template>
        </el-table-column>
        <el-table-column label="预览图(查看大图)" align="center">
          <template slot-scope="scope">
            <el-image
              class="table-td-thumb"
              :src="$utils.formatUrl(scope.row.img)"
              :preview-src-list="[$utils.formatUrl(scope.row.img)]"
            ></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="发布时间"></el-table-column>
        <!-- <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.state==='成功'?'success':(scope.row.state==='失败'?'danger':'')"
            >{{scope.row.state}}</el-tag>
          </template>
        </el-table-column>-->
        <el-table-column label="操作" width="180" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              icon="el-icon-edit"
              @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button>
            <el-button
              type="text"
              icon="el-icon-delete"
              class="red"
              @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :current-page="query.page"
          :page-size="query.pageSize"
          :total="total"
          @current-change="handlePageChange"
        ></el-pagination>
      </div>
    </div>

    <!-- 编辑弹出框 -->
    <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
      <el-form ref="form" :model="form" label-width="70px">
        <el-form-item label="标题">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="用户ID">
          <el-input v-model="form.uid"></el-input>
        </el-form-item>
        <el-form-item label="视频地址">
          <el-input v-model="form.url"></el-input>
        </el-form-item>
        <el-form-item label="预览图">
          <el-input v-model="form.img"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveEdit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "basetable",
  data() {
    return {
      query: {
        name: "",
        page: 1,
        type: null,
        uid:'',
        pageSize: 10
      },
      tableData: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      total: 0,
      form: {},
      idx: -1,
      id: -1,
      typeList: [],
      subTypeList: [],
      type_1: ""
    };
  },
  created() {
    this.getType();
    this.getData();
  },
  methods: {
    
    getType() {
      this.$api.type.list({}).then(res => {
        this.typeList = res.data;
      });
    },
    // 获取 easy-mock 的模拟数据
    getData() {
      this.$api.video.list(this.query).then(res => {
        this.tableData = res.data;
        this.total = res.total || 500;
      });
    },
    changeType(index) {
      this.query.type = "";
      this.subTypeList = this.typeList[index].sub_type;
    },
    // 触发搜索按钮
    handleSearch() {
      this.$set(this.query, "page", 1);
      this.getData();
    },
    // 删除操作
    handleDelete(index, row) {
      // 二次确认删除
      this.$confirm("确定要删除吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          var arr = [row.id];
          this.$api.video.delete({ ids: arr }).then(res => {
            if (res.code == 1) {
              this.$message.error("删除失败");
              return;
            }
            this.getData();
            this.$message.success("删除成功");
          });
        })
        .catch(() => {});
    },
    // 多选操作
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delAllSelection() {
      const length = this.multipleSelection.length;
      var arr = [];
      this.delList = this.delList.concat(this.multipleSelection);
      for (let i = 0; i < length; i++) {
        arr.push(this.multipleSelection[i].id);
      }
      this.$confirm("确定要删除吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          this.$api.video.delete({ ids: arr }).then(res => {
            if (res.code == 1) {
              this.$message.error("删除失败");
              return;
            }
            this.getData();
            this.$message.success("删除成功");
          });
        })
        .catch(() => {});
      this.multipleSelection = [];
    },
    // 编辑操作
    handleEdit(index, row) {
      this.idx = index;
      this.form = row;
      this.editVisible = true;
    },
    // 保存编辑
    saveEdit() {
      this.$api.video.update(this.form).then(res => {
        if (res.code == 1) {
          this.$message.error(`修改失败`);
          return;
        }
        this.editVisible = false;
        this.$message.success(`修改成功`);
        this.$set(this.tableData, this.idx, this.form);
      });
    },
    // 分页导航
    handlePageChange(val) {
      this.$set(this.query, "page", val);
      this.getData();
    },
    
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 100px;
  display: inline-block;
}
.input-title{
  width:200px;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
.mr10 {
  margin-right: 10px;
}
.table-td-thumb {
  display: inline-block;
  margin: auto;
  height: 60px;
  width: 80px;
}
</style>



